.Skills {
    padding: 0 0em 0 0em;
    color: white;
    /* height: 50vh; */
    height: 100vh;
    text-align: center;
    /* background: #171c45;
    background: -webkit-linear-gradient(#171c45 80%, #470c64 100%); */
    font-size: 3em;
    display: grid;
    grid-template-columns: 1em auto 1em;
    grid-template-rows: .2fr 1fr;
    grid-row: 1 / 2;
    /* z-index: 2; */

    /* border: 1px solid pink; */
}
.Skill_top {
    padding: 1em 0 1em 0;
    grid-row: 1 / 2;
    grid-column: 2 / 3;

    /* border: 1px solid pink; */
}
.Skill_bottom {
    padding: 5em 5em 5em 5em;
    grid-row: 2 / 3;
    grid-column: 2 / 3;
    font-size: 18px;
    gap: 2em;
    display: flex;
    align-items: center;
    overflow-x: scroll;
    scroll-behavior: smooth;
    -ms-overflow-style: none;
    scrollbar-width: none;
    
    /* border: 1px solid green; */
}
.Skill_bottom::-webkit-scrollbar {
    display: none;
}

/* .carousel {
    border: 1px solid red;
    width: 90%;
} */

.skill_carousel_item {
    display: grid;
    padding: 70px;
    transition: transform .2s;
}

.skill_carousel_item:hover {
    transform: scale(1.3);
}

.left_button {
    display: flex;
    left: 1em;
    position: absolute;
    justify-content: center;
    align-items: center;
    z-index: 1;
    cursor: pointer;

}
.right_button {
    display: flex;
    right: 1em;
    position: absolute;
    justify-content: center;
    align-items: center;
    z-index: 1;
    cursor: pointer;

}
#right_arrow {
    transform: rotate(-90deg);
}
#left_arrow {
    transform: rotate(90deg);
}